/* Resetear estilos por defecto */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Estilos globales */
body {
  min-width: 320px;
  min-height: 100vh;
  background-color: #1a1a1a;
  color: #ffffff;
  font-family: 'Bloomer', 'Roboto', 'Helvetica', 'Arial', sans-serif;
}

/* Estilos para el tema Material-UI */
.MuiTypography-root {
  font-family: inherit !important;
}

.MuiButton-root {
  font-family: inherit !important;
}

/* Asegurar que los componentes hereden la fuente del tema */
.mui-component {
  font-family: inherit !important;
}
